<template>
  <view class="index-container">
    <view class="header">
      <BrutalistTitle title="Ê†°Âõ≠Âä®ÊÄÅ" size="large" :rotate="0.5" />
    </view>
    
    <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
    <view v-if="loading" class="loading-container">
      <BrutalistLoading text="Âä†ËΩΩ‰∏≠..." />
    </view>
    
    <!-- Âä®ÊÄÅÂàóË°® -->
    <view v-else-if="postList.length > 0" class="post-list">
      <view v-for="(item, index) in postList" :key="item._id" class="post-item-container">
        <BrutalistCard :rotate="(index % 2 === 0) ? -0.5 : 0.5" class="post-item">
          <!-- Áî®Êà∑‰ø°ÊÅØ -->
          <view class="user-info" @tap="goToUserProfile(item.userId)">
            <BrutalistAvatar 
              :src="item.userInfo.avatar" 
              :text="item.userInfo.nickname" 
              size="medium"
            />
            <view class="user-detail">
              <text class="nickname">{{ item.userInfo.nickname }}</text>
              <text class="school" v-if="item.userInfo.school">{{ item.userInfo.school }}</text>
              <text class="time">{{ formatTime(item.createTime) }}</text>
            </view>
          </view>
          
          <!-- Âä®ÊÄÅÂÜÖÂÆπ -->
          <view class="post-content">
            <text class="content-text" v-if="item.content">{{ item.content }}</text>
            
            <!-- ÂõæÁâáÂàóË°® -->
            <view class="image-grid" v-if="item.images && item.images.length">
              <view 
                v-for="(img, imgIndex) in item.images" 
                :key="imgIndex"
                class="image-item"
                @tap="previewImage(item.images, imgIndex)"
              >
                <image :src="img" mode="aspectFill" />
              </view>
            </view>
          </view>
          
          <!-- ‰∫íÂä®Âå∫Âüü -->
          <view class="post-actions">
            <view class="action-item" @tap="likePost(item._id, item.isLiked, index)">
              <text class="action-icon">{{ item.isLiked ? '‚ù§Ô∏è' : 'ü§ç' }}</text>
              <text class="action-text">{{ item.likes || 0 }}</text>
            </view>
            
            <view class="action-item" @tap="commentPost(item._id)">
              <text class="action-icon">üí¨</text>
              <text class="action-text">{{ item.comments || 0 }}</text>
            </view>
            
            <view class="action-item" @tap="sharePost(item._id)">
              <text class="action-icon">üîó</text>
              <text class="action-text">ÂàÜ‰∫´</text>
            </view>
          </view>
        </BrutalistCard>
      </view>
    </view>
    
    <!-- Á©∫Áä∂ÊÄÅ -->
    <view v-else class="empty-container">
      <BrutalistCard :rotate="-1" class="empty-card">
        <text class="empty-text">ËøòÊ≤°ÊúâÂä®ÊÄÅÔºåÂø´Êù•ÂèëÂ∏ÉÁ¨¨‰∏ÄÊù°ÂêßÔºÅ</text>
        <BrutalistButton 
          text="ÂéªÂèëÂ∏É" 
          type="primary" 
          @click="goToPost" 
          class="empty-button"
        />
      </BrutalistCard>
    </view>
    
    <!-- Âä†ËΩΩÊõ¥Â§ö -->
    <view v-if="postList.length > 0 && !isEnd" class="load-more">
      <BrutalistButton 
        text="Âä†ËΩΩÊõ¥Â§ö" 
        type="secondary" 
        @click="loadMore" 
        :loading="loadingMore"
      />
    </view>
    
    <!-- Â∑≤Âà∞Â∫ïÈÉ® -->
    <view v-if="isEnd && postList.length > 0" class="no-more">
      <text class="no-more-text">‚Äî Â∑≤ÁªèÂà∞Â∫ïÂï¶ ‚Äî</text>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      postList: [],
      loading: true,
      loadingMore: false,
      isEnd: false,
      currentPage: 1,
      pageSize: 10
    }
  },
  onLoad() {
    // Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
    this.checkLoginStatus()
    // Âä†ËΩΩÂä®ÊÄÅÂàóË°®
    this.loadPosts()
  },
  onPullDownRefresh() {
    // ‰∏ãÊãâÂà∑Êñ∞
    this.refreshData()
  },
  onReachBottom() {
    // ‰∏äÊãâÂä†ËΩΩÊõ¥Â§ö
    this.loadMore()
  },
  methods: {
    // Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
    checkLoginStatus() {
      const token = uni.getStorageSync('token')
      if (!token) {
        // Ê≤°ÊúâtokenÔºåË∑≥ËΩ¨Âà∞ÁôªÂΩïÈ°µ
        uni.redirectTo({
          url: '/pages/login/login'
        })
        return false
      }
      return true
    },
    
    // Âä†ËΩΩÂä®ÊÄÅÂàóË°®
    async loadPosts(isRefresh = false) {
      if (isRefresh) {
        this.currentPage = 1
        this.isEnd = false
      }
      
      try {
        const token = uni.getStorageSync('token')
        const result = await uniCloud.callFunction({
          name: 'get-posts',
          data: {
            token,
            page: this.currentPage,
            pageSize: this.pageSize
          }
        })
        
        if (result.result.code === 0) {
          const { posts, total, totalPages } = result.result.data
          
          if (isRefresh) {
            this.postList = posts
          } else {
            this.postList = [...this.postList, ...posts]
          }
          
          // Âà§Êñ≠ÊòØÂê¶Â∑≤Âä†ËΩΩÂÖ®ÈÉ®Êï∞ÊçÆ
          if (this.currentPage >= totalPages) {
            this.isEnd = true
          }
        } else {
          uni.showToast({
            title: result.result.message || 'Âä†ËΩΩÂ§±Ë¥•',
            icon: 'none'
          })
        }
      } catch (error) {
        console.error('Âä†ËΩΩÂä®ÊÄÅÂ§±Ë¥•:', error)
        uni.showToast({
          title: 'Âä†ËΩΩÂ§±Ë¥•',
          icon: 'none'
        })
      } finally {
        this.loading = false
        this.loadingMore = false
        if (isRefresh) {
          uni.stopPullDownRefresh()
        }
      }
    },
    
    // Âà∑Êñ∞Êï∞ÊçÆ
    refreshData() {
      this.loadPosts(true)
    },
    
    // Âä†ËΩΩÊõ¥Â§ö
    loadMore() {
      if (this.loadingMore || this.isEnd) return
      
      this.loadingMore = true
      this.currentPage++
      this.loadPosts()
    },
    
    // ÁÇπËµûÂä®ÊÄÅ
    async likePost(postId, isLiked, index) {
      if (!this.checkLoginStatus()) return
      
      try {
        const token = uni.getStorageSync('token')
        const result = await uniCloud.callFunction({
          name: 'like-post',
          data: {
            token,
            postId
          }
        })
        
        if (result.result.code === 0) {
          const { isLiked: newIsLiked, likes } = result.result.data
          this.postList[index].isLiked = newIsLiked
          this.postList[index].likes = likes
        } else {
          uni.showToast({
            title: result.result.message || 'Êìç‰ΩúÂ§±Ë¥•',
            icon: 'none'
          })
        }
      } catch (error) {
        console.error('ÁÇπËµûÂ§±Ë¥•:', error)
        uni.showToast({
          title: 'Êìç‰ΩúÂ§±Ë¥•',
          icon: 'none'
        })
      }
    },
    
    // ËØÑËÆ∫Âä®ÊÄÅÔºàË∑≥ËΩ¨Âà∞ËØÑËÆ∫È°µÔºâ
    commentPost(postId) {
      uni.navigateTo({
        url: `/pages/comments/comments?postId=${postId}`
      })
    },
    
    // ÂàÜ‰∫´Âä®ÊÄÅ
    sharePost(postId) {
      // TODO: ÂÆûÁé∞ÂàÜ‰∫´ÂäüËÉΩ
      uni.showToast({
        title: 'ÂàÜ‰∫´ÂäüËÉΩÂºÄÂèë‰∏≠',
        icon: 'none'
      })
    },
    
    // Ë∑≥ËΩ¨Âà∞Áî®Êà∑‰∏ªÈ°µ
    goToUserProfile(userId) {
      // TODO: ÂÆûÁé∞Áî®Êà∑‰∏ªÈ°µ
      uni.showToast({
        title: 'Áî®Êà∑‰∏ªÈ°µÂºÄÂèë‰∏≠',
        icon: 'none'
      })
    },
    
    // È¢ÑËßàÂõæÁâá
    previewImage(images, current = 0) {
      uni.previewImage({
        current,
        urls: images
      })
    },
    
    // Ê†ºÂºèÂåñÊó∂Èó¥
    formatTime(time) {
      const now = new Date()
      const date = new Date(time)
      const diff = now - date
      
      // ‰∏ÄÂàÜÈíüÂÜÖ
      if (diff < 60 * 1000) {
        return 'ÂàöÂàö'
      }
      
      // ‰∏ÄÂ∞èÊó∂ÂÜÖ
      if (diff < 60 * 60 * 1000) {
        return Math.floor(diff / (60 * 1000)) + 'ÂàÜÈíüÂâç'
      }
      
      // ‰∏ÄÂ§©ÂÜÖ
      if (diff < 24 * 60 * 60 * 1000) {
        return Math.floor(diff / (60 * 60 * 1000)) + 'Â∞èÊó∂Ââç'
      }
      
      // ‰∏Ä‰∏™ÊúàÂÜÖ
      if (diff < 30 * 24 * 60 * 60 * 1000) {
        return Math.floor(diff / (24 * 60 * 60 * 1000)) + 'Â§©Ââç'
      }
      
      // Ê†ºÂºèÂåñÊó•Êúü
      return `${date.getFullYear()}-${(date.getMonth() + 1).toString().padStart(2, '0')}-${date.getDate().toString().padStart(2, '0')}`
    },
    
    // Ë∑≥ËΩ¨Âà∞ÂèëÂ∏ÉÈ°µÈù¢
    goToPost() {
      uni.navigateTo({
        url: '/pages/post/post'
      })
    },
    
    // Ë∑≥ËΩ¨Âà∞‰∏™‰∫∫‰∏≠ÂøÉ
    goToProfile() {
      uni.navigateTo({
        url: '/pages/profile/profile'
      })
    }
  }
}
</script>

<style>
.index-container {
  min-height: 100vh;
  background-color: #FFFFFF;
  padding: 20rpx;
}

.header {
  padding: 20rpx 0;
  margin-bottom: 30rpx;
}

.loading-container, .empty-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 400rpx;
}

.empty-card {
  width: 90%;
  padding: 60rpx 40rpx;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.empty-text {
  font-size: 32rpx;
  color: #333333;
  margin-bottom: 40rpx;
  text-align: center;
}

.empty-button {
  width: 200rpx;
}

.post-list {
  margin-bottom: 40rpx;
}

.post-item-container {
  margin-bottom: 30rpx;
}

.post-item {
  padding: 30rpx;
}

.user-info {
  display: flex;
  align-items: center;
  margin-bottom: 20rpx;
}

.user-detail {
  margin-left: 20rpx;
  flex: 1;
}

.nickname {
  font-size: 32rpx;
  font-weight: 700;
  color: #000000;
  display: block;
  margin-bottom: 5rpx;
}

.school {
  font-size: 26rpx;
  color: #666666;
  display: block;
  margin-bottom: 5rpx;
}

.time {
  font-size: 24rpx;
  color: #999999;
}

.post-content {
  margin-bottom: 20rpx;
}

.content-text {
  font-size: 30rpx;
  color: #333333;
  line-height: 1.6;
  display: block;
  margin-bottom: 20rpx;
}

.image-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 10rpx;
  margin: 0 -5rpx;
}

.image-item {
  width: calc(33.33% - 10rpx);
  height: 200rpx;
  margin: 5rpx;
  border: 4rpx solid #000000;
  box-shadow: 6rpx 6rpx 0px #000000;
  overflow: hidden;
}

.image-item image {
  width: 100%;
  height: 100%;
}

.post-actions {
  display: flex;
  justify-content: space-around;
  padding-top: 20rpx;
  border-top: 4rpx solid #000000;
}

.action-item {
  display: flex;
  align-items: center;
  padding: 10rpx 20rpx;
}

.action-icon {
  font-size: 32rpx;
  margin-right: 10rpx;
}

.action-text {
  font-size: 26rpx;
  color: #333333;
}

.load-more {
  display: flex;
  justify-content: center;
  padding: 20rpx;
}

.no-more {
  display: flex;
  justify-content: center;
  padding: 30rpx;
}

.no-more-text {
  font-size: 28rpx;
  color: #999999;
}
</style>